<template>
  <div class="settingItem">
    <div class="content">
      <el-row :gutter="20">
        <el-col :span="12" :offset="5">
          <el-form label-width="80px" :model="accountInfo" :rules="rules" ref="accountInfo">
            <el-form-item label="用户名" prop="username">
              <el-input v-model="accountInfo.username" placeholder="请输入用户名" clearable></el-input>
            </el-form-item>
            <el-form-item label="邮箱" prop="email">
              <el-input v-model="accountInfo.email" placeholder="请输入邮箱" clearable></el-input>
            </el-form-item>
            <el-form-item label="手机" prop="tel">
              <el-input v-model="accountInfo.tel" placeholder="请输入手机号码" clearable></el-input>
            </el-form-item>
            <el-form-item label="密码" prop="password">
              <el-input
                type="password"
                v-model="accountInfo.password"
                placeholder="请输入密码"
                clearable
              ></el-input>
            </el-form-item>
            <el-form-item label="确认密码" prop="repassword">
              <el-input type="password" v-model="repassword" placeholder="请确认密码" clearable></el-input>
            </el-form-item>
            <el-form-item>
              <el-button type="success" @click="funSubmit('accountInfo')">提交</el-button>
            </el-form-item>
          </el-form>
        </el-col>
      </el-row>
    </div>
  </div>
</template>

<script>
import Cookies from "vue-cookies";
export default {
  name: "settingItem",
  data() {
    /**
     * 手机号验证
     */
    var telValidate = (rule, value, callback) => {
      if (value === "") {
        callback(new Error("请输入手机号码"));
      } else {
        if (!/^1[34578]\d{9}$/.test(value)) {
          callback(new Error("请输入正确的手机号码"));
        } else {
          callback();
        }
      }
    };
    var repassword = (rule, value, callback) => {
      if (this.repassword === "") {
        callback(new Error("请再次输入密码"));
      } else if (this.repassword !== this.accountInfo.password) {
        callback(new Error("两次输入密码不一致!"));
      } else {
        callback();
      }
    };
    return {
      id: "",
      type: "",
      repassword: "",
      accountInfo: {
        username: "",
        password: "",
        question: "",
        answer: "",
        email: "",
        tel: ""
      },
      rules: {
        username: [
          { required: true, message: "请输入用户名", trigger: "blur" }, //非空验证
          { min: 3, max: 15, message: "长度在 3 到 15 个字符", trigger: "blur" } //长度验证
        ],
        email: [
          { required: true, message: "请输入邮箱", trigger: "blur" }, //非空验证
          {
            type: "email",
            message: "请输入正确的邮箱",
            trigger: "blur"
          }
        ],
        tel: [
          { required: true, message: "请输入手机号码", trigger: "blur" },
          { validator: telValidate, trigger: "blur" } //非空验证
        ],
        repassword: [{ validator: repassword, trigger: "blur" }],
        password: [
          { required: true, message: "请输入密码", trigger: "blur" }, //非空验证
          { min: 6, max: 18, message: "长度在 6 到 18 个字符", trigger: "blur" } //长度验证
        ]
      }
    };
  },
  computed: {},
  created() {
    if (Cookies.get("psnid")) {
      this.type = "psn";
      this.id = Cookies.get("psnid");
    } else if (Cookies.get("entid")) {
      this.type = "ent";
      this.id = Cookies.get("entid");
    }
    this.axios
      .post("getAccountInfo", {
        id: this.id,
        type: this.type
      })
      .then(data => {
        if (data.data.status == "ok") {
          this.accountInfo = JSON.parse(data.data.msg)[0].fields;
        }
      });
  },
  methods: {
    funSubmit(formName) {
      this.$refs[formName].validate(valid => {
        if (valid) {
          this.axios
            .post("subAccountInfo", {
              id: this.id,
              type: this.type,
              msg: this.accountInfo
            })
            .then(data => {
              if (data.data.status == "ok") {
                this.$message({
                  type: "success",
                  message: data.data.msg
                });
              }
            });
        }
      });
    }
  }
};
</script>
<style scoped>
.el-dropdown-link {
  cursor: pointer;
  color: #67c23a;
}

.el-icon-arrow-down {
  font-size: 12px;
}

.el-dropdown-menu__item:focus,
.el-dropdown-menu__item:not(.is-disabled):hover {
  background-color: #ecf5ff;
  color: #67c23a;
}

.el-row {
  margin-bottom: 20px;

  &:last-child {
    margin-bottom: 0;
  }
}

.content {
  padding: 60px;
  color: #303133;
}

.content-left {
  padding-bottom: 20px;
}

.content-left span {
  font-size: 18px;
  color: #606266;
}

.content-left p {
  font-size: 14px;
  color: #909399;
}

.content-right .box-card {
  background: #f8f8f9;
}

article {
  padding: 10px;
}

.base-info-item {
  font-size: 14px;
  margin: 10px 0;
}
.for-info {
  border-bottom: 1px solid #ddd;
  display: flex;
}
.for-item {
  width: 33%;
  font-size: 14px;
  margin: 10px;
  margin-right: 80px;
}
.for-info-item {
  font-size: 14px;
  margin: 10px 0;
}
</style>